import React, { useEffect } from "react";
import "./style.css"
export default function PercentCircle(props) {

    var {color , percent , size} = props


  return (
      <div style={props.styleCss} className={`c100 p${percent} ${color}`}>
        <span>{percent}%</span>
        <div className="slice">
          <div className="bar" />
          <div className="fill" />
        </div>
      </div>

  );
}
