import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import PercentCircle from "../../../components/PercentCircle/PercentCircleComponent";
export default function Item(props) {
  var setPercent = [
    { color: "blue", percent: "90" },
    { color: "orange", percent: "10" },
    { color: "green", percent: "30" },
  ];

  return (
    <div
      className="wrapper"
      style={{
        display: "flex",
        "justify-content": "center",
      }}
    >
      <div className="item-overview">
        <PercentCircle
          styleCss={{ margin: "auto" }}
          color={"blue"}
          percent={90}
        ></PercentCircle>
        <span >
          Bài Test đã tham gia
        </span>
      </div>
      <div className="item-overview">
        <PercentCircle
          styleCss={{ margin: "auto" }}
          color={"orange"}
          percent={50}
        ></PercentCircle>
        <span >
          Bài Test đã hoàn thành
        </span>
      </div>
      <div className="item-overview">
        <PercentCircle
          styleCss={{ margin: "auto" }}
          color={"green"}
          percent={30}
        ></PercentCircle>
        <span >
          Tỉ lệ đúng
        </span>
      </div>
    </div>
  );
}
